import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { parse } from 'papaparse';
import BarChart from '../Charts/BarChart/BarChart';
import LineChart from '../Charts/LineChart/LineChart';
import PieChart from '../Charts/PieChart/PieChart';
import Dougnut from '../Charts/DougnutChart/DougnutChart';
//import RangeChart from '../Charts/RangeChart/RangeChart';
import './ChartSelection.css';

function ChartSelection() {
  const [selectedChart, setSelectedChart] = useState('Bar Chart');
  const [selectedStatOptions, setSelectedStatOptions] = useState(['First']); // Default to 'First'
  const [data, setData] = useState([]);

  const handleChartChange = (event) => {
    setSelectedChart(event.target.value);
  };

  const handleStatOptionChange = (event) => {
    const option = event.target.value;
    if (selectedStatOptions.includes(option)) {
      // If the option is already selected, deselect it
      setSelectedStatOptions(selectedStatOptions.filter((item) => item !== option));
    } else {
      // If the option is not selected, select it and deselect all other options
      setSelectedStatOptions([option]);
    }
  };
  useEffect(() => {
    // Fetch your data here
    const sheetUrl =
      'https://docs.google.com/spreadsheets/d/e/2PACX-1vRZ88nEq5vzLNdVE68AR5kMzS5HveQgQUxXF9rNX-9yhmv2jf2Bohwja3j-vDzUcGbO2ziCZ-Mv3v4J/pub?gid=1125870162&single=true&output=csv';
    axios
      .get(sheetUrl)
      .then((response) => {
        const parsedData = parse(response.data, { header: true });
        setData(parsedData.data);
      })
      .catch((error) => {
        console.error(error);
      });
  }, [selectedChart]);
  
  return (
    <div className='charts-selection'>
      <div className='chart-selector'>
        <label>Select a Chart:</label>
        <select value={selectedChart} onChange={handleChartChange}>
          <option value='Bar Chart'>Bar Chart</option>
          <option value='Line Chart'>Line Chart</option>
          <option value='Pie Chart'>Pie Chart</option>
          <option value='doughnut Chart'>Doughnut Chart</option>
        </select>
      </div>
      <div className='stat-options'>
        <label>Select Stat Options:</label>
        <div>
          <label>
            <input
              type='checkbox'
              value='First'
              checked={selectedStatOptions.includes('First')}
              onChange={handleStatOptionChange}
            />
            First Wins
          </label>
          <label>
            <input
              type='checkbox'
              value='Second'
              checked={selectedStatOptions.includes('Second')}
              onChange={handleStatOptionChange}
            />
            Second Wins
          </label>
          <label>
            <input
              type='checkbox'
              value='Third'
              checked={selectedStatOptions.includes('Third')}
              onChange={handleStatOptionChange}
            />
            Third Wins
          </label>
          <label>
            <input
              type='checkbox'
              value='Fourth'
              checked={selectedStatOptions.includes('Fourth')}
              onChange={handleStatOptionChange}
            />
            Fourth Wins
          </label>
          <label>
            <input
              type='checkbox'
              value='O/T Wins'
              checked={selectedStatOptions.includes('O/T Wins')}
              onChange={handleStatOptionChange}
            />
            O/T Wins
          </label>
          <label>
            <input
              type='checkbox'
              value='O/T Loss'
              checked={selectedStatOptions.includes('O/T Loss')}
              onChange={handleStatOptionChange}
            />
            O/T Losses
          </label>
          <label>
            <input
              type='checkbox'
              value='O/T Games'
              checked={selectedStatOptions.includes('O/T Games')}
              onChange={handleStatOptionChange}
            />
            O/T Games
          </label>
          <label>
            <input
              type='checkbox'
              value='TotalPoints'
              checked={selectedStatOptions.includes('TotalPoints')}
              onChange={handleStatOptionChange}
            />
            Total Points
          </label>
        </div>
      </div>
      <div className='right-chart'>
        {selectedChart === 'Bar Chart' && (
          <BarChart data={data} selectedStatOptions={selectedStatOptions} />
        )}
        {selectedChart === 'Line Chart' && (
          <LineChart data={data} selectedStatOptions={selectedStatOptions} />
        )}
        {selectedChart === 'Pie Chart' && (
          <PieChart data={data} selectedStatOptions={selectedStatOptions} />
        )}
        {selectedChart === 'doughnut Chart' && (
          <Dougnut data={data} selectedStatOptions={selectedStatOptions} />
        )}
      </div>
    </div>
  );
}

export default ChartSelection;
