import React, { useState, useEffect } from 'react';
import axios from 'axios';
import { parse } from 'papaparse';
import TitleBar from './Compon/TitleBar/TitleBar';
import PageButtons from './Compon/PageButton/PageButtons';
import ChartSelection from './Compon/ChartSelections/ChartSelection';
import PlayerCard from './Compon/PlayerCards/PlayerCard';
import './App.css';

function App() {
  const [data, setData] = useState([]);
  const [refreshTime, setRefreshTime] = useState(120);

  const fetchData = () => {
    const sheetUrl = 'https://docs.google.com/spreadsheets/d/e/2PACX-1vRZ88nEq5vzLNdVE68AR5kMzS5HveQgQUxXF9rNX-9yhmv2jf2Bohwja3j-vDzUcGbO2ziCZ-Mv3v4J/pub?gid=1125870162&single=true&output=csv';
    axios.get(sheetUrl)
      .then((response) => {
        const parsedData = parse(response.data, { header: true });
        setData(parsedData.data);
        setRefreshTime(120);
      })
      .catch((error) => {
        console.error(error);
      });
  };

  const handleRefresh = () => {
    fetchData();
  };

  useEffect(() => {
    fetchData();

    const intervalId = setInterval(() => {
      fetchData();
    }, 120000);

    const timerInterval = setInterval(() => {
      setRefreshTime((prevTime) => prevTime - 1);
    }, 1000);

    return () => {
      clearInterval(intervalId);
      clearInterval(timerInterval);
    };
  }, []);

  return (
    <div className="page">
      <TitleBar refreshTime={refreshTime} onTimeout={handleRefresh} />
      <div className='buttonHolder'>
        <PageButtons />
      </div>
      <div className="pageHolder">
      <div className='chart'>
    <ChartSelection data={data} />
  </div>

  <div className='player-cards'>
    {data.map((player, index) => (
      <PlayerCard key={index} player={player} />
    ))}
  </div>
</div>
    </div>
  );
}

export default App;