import React from 'react';
import PropTypes from 'prop-types';
import './PlayerCard.css'; 

const PlayerCard = ({ player }) => {
  if (!player) {
    return null; 
  }

  const getColorBasedOnRange = (value, minValue, maxValue) => {
    // Use a small threshold for comparison due to potential precision issues
    const threshold = 0.0001;
    
    // Check if the value is close to the lowest or the highest
    const isLowest = Math.abs(value - minValue) < threshold;
    const isHighest = Math.abs(value - maxValue) < threshold;
  
    // If it's close to the lowest, set the color to 'red'; if it's close to the highest, set the color to 'green'; otherwise, use a default color
    return isLowest ? 'red' : (isHighest ? 'green' : 'defaultColor'); // replace 'defaultColor' with your default color
  };
  
  const getStatStyle = (stat) => {
    const statValue = player[stat];
    const statValues = Object.values(player);
    const minValue = Math.min(...statValues);
    const maxValue = Math.max(...statValues);
    const color = getColorBasedOnRange(statValue, minValue, maxValue);
  
    return {
      backgroundColor: color,
      color: 'white', // Set the text color to white
    };
  };

  // Dynamically generate stats based on player object keys
  const stats = Object.keys(player)
    .filter(key => key !== 'PlayerName' && key !== 'FavCar' && key !== 'TotalPoints');
    return (
        <div className="player-card">
          <div className="player-info">
            <h2 className="player-name">{player.PlayerName}</h2>
            <p className="player-fav-car">Fav Car: {player.FavCar}</p>
            <div className="player-stats">
              {stats.map(stat => (
                <p key={stat} style={getStatStyle(stat)}>
                  {stat}: {player[stat]}
                </p>
              ))}
            </div>
          </div>
        </div>
      );
    };

PlayerCard.propTypes = {
  player: PropTypes.shape({
    PlayerName: PropTypes.string.isRequired,
    FavCar: PropTypes.string.isRequired,
    FirstPlace: PropTypes.number.isRequired,
    SecondPlace: PropTypes.number.isRequired,
    ThirdPlace: PropTypes.number.isRequired,
    FourthPlace: PropTypes.number.isRequired,
    OTWins: PropTypes.number.isRequired,
    OTLoss: PropTypes.number.isRequired,
    OTGames: PropTypes.number.isRequired,
    TotalPoints: PropTypes.number.isRequired,
  }),
};

export default PlayerCard;